import React from "react";
import styled from "styled-components";
// Icons
import { Icon } from "@iconify/react";
// Media
import GH from "../images/GH.svg";
// Components
import { Card } from "react-bootstrap";

const StyledCardComponent = styled.div`
  .card {
    background: ${({ theme }) => (theme.name === "light" ? "" : "#797B7B")};
    height: max-content;
    width: 22vw;
    box-shadow: ${({ theme }) =>
      theme.name === "light"
        ? "0 3px 10px rgb(0 0 0 / 0.2)"
        : "0 3px 10px rgb(255 255 255 / 0.2)"};

    .card-link {
      text-decoration: none;
      font-size: 1.5rem;
      color: ${({ theme }) => theme.color};

      &:hover {
        color: var(--primary);
      }
    }

    img {
        height: 30vh;
        width: 20vw;
      }

    .card-footer {
      border-top: var(--border);
      background: ${({ theme }) => (theme.name === "light" ? "" : "#404040")};
    }

    .card-body {
      height: 30vh;
    }

  }

  @media screen and (max-width: 800px) {
    .card {
    height: max-content;
    width: 82vw;

    .card-body {
      height: 35vh;
    }
    
      img {
        height: 40vh;
        width: 80vw;
      }
    }

  }
`;

export default function StyledCard({ image, name, description, url, urlSource, pdf }) {
  return (
    <StyledCardComponent>
      <Card>
        <Card.Img
          variant="top"
          src={image ? image : GH}
          alt={name}
          className="mx-auto"
        />
        <Card.Body className="overflow-none text-center">
          <Card.Title>{name}</Card.Title>
          <Card.Text>{description}</Card.Text>
          {pdf !== "" ? (
            <Card.Link href={pdf}>
              {"Read online "}
              <Icon icon="fluent:document-pdf-16-filled" />
            </Card.Link>
          ) : null}
        </Card.Body>
        <Card.Footer className="text-center">
          <Card.Link href={url}>
            {`Open on ${urlSource}`}
            <Icon icon="fluent:link-16-filled" />
          </Card.Link>
        </Card.Footer>
      </Card>
    </StyledCardComponent>
  );
}
