import styled from "styled-components";
// Components
import SocialLinks from "./SocialLinks";

const StyledFooter = styled.footer`
  min-height: var(--min-footer-height);
  background: ${({ theme }) => theme.background};

  a {
    color: ${({ theme }) => theme.name === "light" ? `var(--secondary-dark)` : `var(--secondary-light)`};

  &:hover {
        color: ${({ theme }) => theme.name === "light" ? `var(--primary-light)` : `var(--primary-dark)`} !important;
      }
  }
`;

export default function Footer() {
  return (
    <StyledFooter className="d-flex align-items-center justify-content-center p-2">
      <SocialLinks />
    </StyledFooter>
  );
}
